<template>
    <div>
        <!-- 购物车 -->
        <van-empty v-if="!list.length" description="空空入也,去首页买点吧" />
        <van-swipe-cell v-for="item in list" :key="item.id">
            <van-card
            class="active"
            tag="已选"
            :price="item.pri"
            desc="优质水果"
            :title="item.name"
            :thumb="item.src"
            @click="clickHandle(item.id)"
            >
                <template #tags>
                    <van-tag plain type="danger">自营</van-tag>
                    <van-tag plain type="danger">生鲜</van-tag>
                </template>
                <template #footer>
                    <van-button size="mini" @click="sub(item.id)">-</van-button>
                    <van-button size="mini" disabled>{{item.num}}</van-button>
                    <van-button size="mini" @click="add(item.id)">+</van-button>
                </template>
            </van-card>
            <template #right>
                <van-button square text="删除" type="danger" class="delete-button" 
                @click="del(item.id)"
                />
            </template>
        </van-swipe-cell>

        <van-tabbar class="tab">
            <span>已选: {{totalNum}}</span>
            <div>
                <span>￥{{total}}</span>
                <van-button type="danger"  size="small" @click="go"
                :disabled="!list.length"
                >立即下单</van-button>
            </div>
        </van-tabbar>
    </div>
</template>

<script>
import {mapState, mapGetters} from "vuex"
export default {

    computed: {
        ...mapState("shoppingCar", ["list"]),
        ...mapGetters("shoppingCar", ["total", "totalNum"]),
    },
    methods: {
        clickHandle(id) {
            
        },
        sub(id) {
            this.$store.commit("shoppingCar/sub" ,
            id)
        },
        add(id) {
            this.$store.commit("shoppingCar/add" ,
            id)
        },
        del(id) {
            this.$store.commit("shoppingCar/del" ,
            id)
        }, 
        go() {
            this.$router.push({
                name: "confirm"
            })
        }
    }
}
</script>

<style lang="scss" scoped>
    .goods-card {
margin: 0;
background-color: #fff;
}

.delete-button {
height: 100%;
}
.tab{
    align-items: center;
    justify-content: space-around;
    &>div{
        display: flex;
        align-items: center;
        span{
            margin-right: 15px;
        }
    }
    }
    .van-card{
    background: #fff;
    }
    .active.van-card{
        background-color: #fafafa;
    }
</style>